export default {
	data() {
		return {
			dataArr: []
		}
	},
	onLoad() {
		this.getMyCert()
	},
	methods: {

		getMyCert() {
			var that = this;
			var loginkey = uni.getStorageSync('loginKey');
			this.$api.post('user!ajaxGetMyCertificate.action', {
				loginKey: loginkey
			}).then(res => {
				if (res.res.status == 0) {
					that.dataArr = res.inf.arr
				} else {

				}
			})
		},
		
		itemClick(item){
			uni.navigateTo({
				url:'./myCertDetail?id='+item.id
			})
		}
	}

}
